import {
    MapContainer,
    TileLayer,
    ZoomControl,
    ScaleControl,
    useMapEvents,
} from "react-leaflet";
import "leaflet/dist/leaflet.css";
import "./Map.css";
import Logo from "../../assets/logo/Logo.png";
import { useState } from "react";
import MapOverlayComponent from "./MapOverlayComponent";
import MapMarkerComponent from "./MapMarkerComponent";
import LocationSeeker from "./MapLocationSeeker";
import MapLocationMarker from "./MapLocationMarker";
import MapDraggableMarker from "./MapDraggableMarker";
import MapLeafletGeosearch from "./MapLeafletGeosearch";

export default function MapComponent(props) {
    const [maker, setMaker] = useState(null);

    const handleClickMarker = (item) => {
        console.log(item, "handle click marker");
        if (maker === null) {
            setMaker(item);
        } else {
            if (maker.Company_Name === item.Company_Name) {
                setMaker(null);
            } else {
                setMaker(item);
            }
        }
    };

    function MapListner() {
        useMapEvents({
            click: () => {
                setMaker(null);
            },
        });
        return null;
    }

    console.log(props.position, "inside map component line 44");

    return (
        <>
            <div className="mapContainer">
                <MapOverlayComponent
                    handleClick={() => setMaker(null)}
                    data={maker}
                    logo={Logo}
                />
                <MapContainer
                    className="mapBlock markercluster-map"
                    center={[28.3949, 84]}
                    zoom={7}
                    scrollWheelZoom={true}
                    zoomControl={false}
                    eventHandlers={{
                        click: (e) => {
                            console.log(e.lat, "map contaner");
                            setMaker(null);
                        },
                    }}
                >
                    <TileLayer
                        attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                        url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                    />
                    <ScaleControl position="bottomright" />
                    <ZoomControl position="bottomright" />
                    <MapListner />
                    {props.onClickMark && (
                        <MapLocationMarker
                            locationSet={props.locationSet}
                            pos={props.position}
                        />
                    )}
                    {props.drawMark && (
                        <MapMarkerComponent
                            data={props.data}
                            handleClick={handleClickMarker}
                        />
                    )}
                    {/* {props.currentPosition && <MapDraggableMarker />} */}
                    <LocationSeeker search={props.search} />
                    {/* <MapLeafletGeosearch /> */}
                </MapContainer>
            </div>
        </>
    );
}
